// Inspired by https://frankindev.com/2017/03/18/syntax-highlight-with-rouge-in-jekyll/

figure.highlight,
pre.highlight {
  padding: 1rem 1.5rem;
  margin: 1.5rem 0;
  overflow: auto;
  line-height: 1.4rem;
  position: relative;
}

.highlighter-rouge {
  font-weight: 300;
  position: relative;
}

code.highlighter-rouge,
p kbd {
  color: #cc6666;
  background-color: #f7f7f5;
  padding: 2px 4px;
  border-radius: 2px;
  word-break: break-word;
}

kbd {
  border: 1px solid #2f363d;
  border-radius: 4px;
}

.rouge-table .code pre {
  overflow: hidden;
}

.rouge-table .code pre {
  overflow: hidden;
}

table.rouge-table,
table.rouge-table tr,
table.rouge-table td,
table.rouge-table pre {
  border: 0;
  padding: 0;
  margin: 0;
}

table.rouge-table td.gutter {
  padding-right: .5rem;
  border-right: 1px solid #6a737d;
}

table.rouge-table td.code {
  padding-left: .5rem;
}

figure pre {
  margin-bottom: 0;
}

.lineno {
  text-align: right;
}

/* Base16 Tomorrow Dark by Chris Kempson; https://github.com/idleberg */
.highlight,
.highlight pre,
.highlight table {
  border: none;
  background: $code-background-color;
  color: #e1e4e8;
  border-radius: 4px;

  code {
    &[data-lang]::before {
      content: attr(data-lang);
      position: absolute;
      right: 4px;
      top: 0;
    }
  }
}



